// components/FilterBar.tsx
import { Button } from "@/components/ui/button";
import { Popover, PopoverTrigger } from "@/components/ui/popover";
import { ChevronsUpDown } from "lucide-react";

import { useState } from "react";

interface OptionType {
  label: string;
  value: any;
}

export type FilterField = {
  label: string;
  name: string;
  type: "text" | "select" | "date";
  options?: OptionType[]; // for 'select' type
};

type Props = {
  config: FilterField[];
  onFilterChange: (filters: Record<string, string>) => void;
  generateReport: () => void;
};

export default function FilterBar({
  config,
  onFilterChange,
  generateReport,
}: Props) {
  const [open, setOpen] = useState(false);
  const [value, setValue] = useState("");

  const [filters, setFilters] = useState<Record<string, string>>({});

  const handleChange = (name: string, value: string) => {
    setFilters((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onFilterChange(filters);
  };

  return (
    <div className="w-full flex items-center justify-between bg-white px-4 ">
      <form
        onSubmit={handleSubmit}
        className="flex flex-wrap gap-4 p-4 bg-white items-center"
      >
        {config.map(({ label, name, type, options }) => (
          <div key={name} className="flex flex-col min-w-[200px] ">
            {type === "select" ? (
              <div className="relative">
                <label className="absolute left-2 -top-2 bg-white text-sm px-1 text-gray-600">
                  {label}
                </label>
                <Popover open={open} onOpenChange={setOpen}>
                  <PopoverTrigger asChild>
                    <Button
                      variant="outline"
                      role="combobox"
                      aria-expanded={open}
                      className="w-[200px] justify-between"
                    >
                      {value
                        ? frameworks.find(
                            (framework) => framework.value === value,
                          )?.label
                        : "Select framework..."}
                      <ChevronsUpDown className="opacity-50" />
                    </Button>
                  </PopoverTrigger>
                  <PopoverContent className="w-[200px] p-0">
                    <Command>
                      <CommandInput
                        placeholder="Search framework..."
                        className="h-9"
                      />
                      <CommandList>
                        <CommandEmpty>No framework found.</CommandEmpty>
                        <CommandGroup>
                          {frameworks.map((framework) => (
                            <CommandItem
                              key={framework.value}
                              value={framework.value}
                              onSelect={(currentValue) => {
                                setValue(
                                  currentValue === value ? "" : currentValue,
                                );
                                setOpen(false);
                              }}
                            >
                              {framework.label}
                              <Check
                                className={cn(
                                  "ml-auto",
                                  value === framework.value
                                    ? "opacity-100"
                                    : "opacity-0",
                                )}
                              />
                            </CommandItem>
                          ))}
                        </CommandGroup>
                      </CommandList>
                    </Command>
                  </PopoverContent>
                </Popover>

                {filters[name] && (
                  <button
                    type="button"
                    onClick={() => handleChange(name, "")}
                    className="absolute right-2 top-1/2 -translate-y-1/2 text-gray-500 hover:text-red-600"
                  >
                    ✕
                  </button>
                )}
              </div>
            ) : (
              <div className="relative">
                <label className="absolute left-2 -top-2 bg-white text-sm px-1 text-gray-600">
                  {label}
                </label>
                <input
                  type={type}
                  value={filters[name] || ""}
                  onChange={(e) => handleChange(name, e.target.value)}
                  className="border rounded px-2 py-4 pr-8"
                  placeholder={name}
                />

                {filters[name] && (
                  <button
                    type="button"
                    onClick={() => handleChange(name, "")}
                    className="absolute right-5 top-1/2 -translate-y-1/2 text-gray-500 hover:text-red-600"
                  >
                    ✕
                  </button>
                )}
              </div>
            )}
          </div>
        ))}

        <Button className=" hover:bg-gray-600" type="submit">
          Apply
        </Button>
      </form>

      <Button className=" hover:bg-gray-600" onClick={() => generateReport()}>
        Generate Report
      </Button>
    </div>
  );
}
